.error {
    font-size: 32px;
    font-weight: 800;
    color: rgb(138, 199, 77);
    text-align: center;
    padding: 50px;
    animation-name: example;
    animation-duration: 4s;
}

@keyframes example {
    from {
        font-size: 32px;
    }

    to {
        font-size: 50px;
    }
}